import React, { useState, useEffect } from "react";
import { collection, addDoc, getDocs} from "firebase/firestore";
import { db } from "./firebase";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import SharedLayout from "./SharedLayout";

function LuckyDrawPage({ guestName }) {
  const [drawResults, setDrawResults] = useState([]);
  const [availableNumbers, setAvailableNumbers] = useState([95, 1, 22, 3, 44]); // 기본 숫자 배열
  const navigate = useNavigate();

      // 뽑힌 숫자를 Firebase에서 가져옴
    const fetchUsedNumbers = async () => {
        const querySnapshot = await getDocs(collection(db, "birthday_page_used_numbers"));
        const usedNumbers = querySnapshot.docs.map((doc) => doc.data().number);
        // 남아 있는 숫자를 계산
        const remainingNumbers = [95, 1, 22, 3, 44].filter((num) => !usedNumbers.includes(num));
        setAvailableNumbers(remainingNumbers);
    };

    const handleDraw = async () => {
      if (availableNumbers.length === 0) {
        Swal.fire("경품 추첨 완료", "모든 숫자가 뽑혔습니다.", "warning");
        return;
      }
  
      const randomIndex = Math.floor(Math.random() * availableNumbers.length);
      const number = availableNumbers[randomIndex]; // 랜덤 숫자 선택
  
      const hasDrawn = drawResults.some((result) => result.name === guestName.name);
      if (hasDrawn) {
        Swal.fire(
          "중복된 이름",
          `${guestName.name}님은 이미 경품 추첨을 완료하셨습니다.`,
          "warning"
        );
        return;
      }
  
      try {
        // Firebase에 뽑힌 숫자와 게스트 이름 기록
        await addDoc(collection(db, "birthday_page_used_numbers"), {
          guestName: guestName.name,
          number,
          timestamp: new Date(),
        });

        await addDoc(collection(db, "birthday_page_lucky_draw"), {
          name: guestName.name,
          number,
          timestamp: new Date(),
        });
  
        // 상태 업데이트
        const updatedNumbers = availableNumbers.filter((num) => num !== number);
        setAvailableNumbers(updatedNumbers);
        fetchDrawResults(); // 기록 업데이트
      } catch (error) {
        console.error("Error saving lucky draw result: ", error);
      }
    };

    const fetchDrawResults = async () => {
      const querySnapshot = await getDocs(collection(db, "birthday_page_lucky_draw"));
      const results = querySnapshot.docs.map((doc) => doc.data());
      setDrawResults(results);
    };
  
    useEffect(() => {
      fetchUsedNumbers(); // Firebase에서 뽑힌 숫자 가져오기
      fetchDrawResults(); // Firebase에서 기록 가져오기
    }, []);

  return (
    <SharedLayout guestName={guestName}>
      <h1 className="mb-4" style = {{fontWeight: "bold"}}>경품 추첨</h1>
      <div className="mb-3">
        <img
          src={`${process.env.PUBLIC_URL}/photos/gift-box.png`}
          alt="gift-box"
          onClick={handleDraw}
          style={{
            cursor: "pointer",
            width: "150px", // 상자 이미지의 너비 조정
            height: "auto", // 높이는 비율 유지
          }}
        />
      </div>
      <h3 style = {{fontWeight: "bold"}}>경품 추첨 기록</h3>
      <ul className="list-group">
        {drawResults.map((result, index) => (
          <li key={index} className="list-group-item">
            {result.name}님이 뽑은 숫자: {result.number}
          </li>
        ))}
      </ul>
      <div className="d-flex justify-content-between mt-5">
      {/* 이전 페이지 버튼 */}
      <button
        className="btn btn-pink mt-5"
        onClick={() => navigate("/invitation")}
      >
        이전 페이지
      </button>
      <button
        className="btn btn-pink mt-5"
        onClick={() => navigate("/order-food")}
      >
        다음 페이지
      </button>
      </div>
    </SharedLayout>
  );
}

export default LuckyDrawPage;