import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import "bootstrap/dist/css/bootstrap.min.css";
import "./PhotoPage.css"; // 추가한 CSS 파일

function PhotoPage({ setGuestName }) {

  const [showHeader, setShowHeader] = useState(true); // 헤더 표시 여부
  const [showPhotos, setShowPhotos] = useState(true); // 사진 표시 여부
  const navigate = useNavigate();


  const handlePhotoClick = (photoIndex) => {
    Swal.fire({
      title: "이름이나 별명을 입력해주세요",
      input: "text",
      inputPlaceholder: "이름을 입력해주세요",
      showCancelButton: true,
      confirmButtonText: "확인",
      cancelButtonText: "취소",
      preConfirm: (value) => {
        const regex = /^[a-zA-Z가-힣0-9]{1,20}$/; // 특수문자 불가, 길이 20자 이하
        if (!regex.test(value)) {
          Swal.showValidationMessage(
            "이름은 한글, 영어, 숫자만 가능하며 20자 이하여야 합니다."
          );
          return false;
        }
        return value;
      },
    }).then((result) => {
      if (result.isConfirmed && result.value) {
        const guestName = result.value;
        setGuestName({ name: guestName, photo: photoIndex });
        navigate("/invitation");
      }
    });
  };

  return (
    <div
    className="photo-page-container"
    style={{
      backgroundColor: "rgb(255 230 240 / 30%)", // 옅은 핑크색 배경
      minHeight: "100vh",
      display: "flex",
      flexDirection: "column", // 세로 정렬
      justifyContent: "center",
      alignItems: "center",
    }}
  >
    {showHeader && (
      <h1
        className={`mb-4 fade-in`}
        style={{
          color: "black", // 글자색
          padding: "1rem", // 텍스트 주변 간격
          marginBottom: "2rem", // 텍스트와 이미지 사이 간격
        }}
      >
        ♡가장 마음에 드는 사진을 골라주세요♡
      </h1>
    )}

{showPhotos && (
  <div className="row g-3 justify-content-center photo-grid">
    {[1, 2, 3, 4, 5, 6].map((_, index) => (
      <div
        key={index}
        className="col-6 col-md-4 photo-slide-in"
        onClick={() => handlePhotoClick(index + 1)}
        style={{ cursor: "pointer" }}
      >
        <img
          src={`${process.env.PUBLIC_URL}/photos/photo${index + 1}.jpg`}
          alt={`photo-${index + 1}`}
          className="img-fluid rounded shadow"
          style={{
            width:
              index === 0
                ? "150px" // photo 1
                : index === 1
                ? "250px" // photo 2
                : index === 2
                ? "150px" // photo 3
                : index === 3
                ? "250px" // photo 4
                : index === 4
                ? "150px" // photo 5
                : "200px", // photo 6
            height: "auto", // 높이는 비율 유지
            transform: `rotate(${index % 2 === 0 ? -10 : 10}deg)`,
          }}
        />
      </div>
    ))}
  </div>
)}
    </div>
  );
}

export default PhotoPage;