import React from "react";
import { useNavigate } from "react-router-dom";
import SharedLayout from "./SharedLayout";

function InvitationPage({ guestName }) {
  const navigate = useNavigate();

  return (
    <SharedLayout guestName={guestName}>
      <h1 className="mb-4" style={{ color: "#fc7fbe", fontWeight: "bold" }}>Happy Birthday 유라</h1>
      <p className="fs-4" style={{ fontWeight: "bold" }}>{guestName.name}님을 초대합니다!</p>
      <p className="fs-3" style={{ fontWeight: "bold" }}>날짜: 2025년 1월 18일 (토) 19시 30분~</p>
      <p className="fs-3" style={{ fontWeight: "bold" }}>장소: 김포집</p>

      <div className="d-flex justify-content-center mt-4">
    {/* 1부 일정 */}
    <div className="me-5">
      <h3 className="fs-3" style={{ color: "#fc7fbe", fontWeight: "bold" }}>1부</h3>
      <ul className="list-unstyled">
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>19시 30분 입장</li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>19시 40분 포토타임</li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>20시 30분 영상시청 및 다과</li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>21시 10분 미니게임</li>
      </ul>
    </div>

    {/* 2부 일정 */}
    <div>
      <h3 className="fs-3" style={{ color: "#fc7fbe", fontWeight: "bold" }}>2부</h3>
      <ul className="list-unstyled">
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>21시 30분 포토타임</li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>21시 40분 경품추첨</li>
        <li style={{ fontSize: "20px" , fontWeight: "bold"}}>22시 00분 저녁</li>
      </ul>
    </div>
  </div>

      <button
        className="btn btn-pink mt-5"
        onClick={() => navigate("/lucky-draw")}
      >
        다음 페이지
      </button>
    </SharedLayout>
  );
}

export default InvitationPage;