import React, { useState, useEffect } from "react";
import { collection, addDoc, getDocs, orderBy, query } from "firebase/firestore";
import { db } from "./firebase";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import SharedLayout from "./SharedLayout";

function OrderFoodPage({ guestName }) {
  const [comment, setComment] = useState("");
  const [foodComments, setFoodComments] = useState([]);
  const navigate = useNavigate();

  const handleCommentSubmit = async () => {
    if (comment.length > 30) {
      Swal.fire("글자수 초과", "음식 이름은 30자 이내로 입력해주세요.", "warning");
      return;
    }

    try {
      await addDoc(collection(db, "birthday_page_foods"), {
        name: guestName.name,
        comment,
        timestamp: new Date(),
      });
      setComment("");
      fetchFoodComments();
      Swal.fire("완료했습니다", "생일파티날 보아요♡", "success");
    } catch (error) {
      console.error("Error adding comment: ", error);
    }
  };

  const fetchFoodComments = async () => {
    const querySnapshot = await getDocs(
      query(collection(db, "birthday_page_foods"), orderBy("timestamp", "desc"))
    );
    const comments = querySnapshot.docs.map((doc) => doc.data());
    setFoodComments(comments);
  };

  useEffect(() => {
    fetchFoodComments();
  }, []);

  return (
    <SharedLayout guestName={guestName}>
      <h1 className="mb-4" style = {{fontWeight: "bold"}}>♡원하는 음식을 적어주세요♡</h1>
      <h5 className="mb-4">(불가할 수도 있습니다)</h5>
      <textarea
        className="form-control mb-3"
        placeholder="예시 : 바른치킨 순살반반 1마리 + 카사바칩"
        value={comment}
        onChange={(e) => setComment(e.target.value)}
      />
      <button className="btn btn-pink mb-4" onClick={handleCommentSubmit}>
        댓글 남기기
      </button>
      <h3>음식 요청 기록</h3>
      <ul className="list-group">
        {foodComments.map((item, index) => (
          <li key={index} className="list-group-item">
            {item.name}: {item.comment}
          </li>
        ))}
      </ul>
      <button
        className="btn btn-pink mt-5"
        onClick={() => navigate("/lucky-draw")}
      >
        이전 페이지
      </button>
    </SharedLayout>
  );
}

export default OrderFoodPage;