import React from "react";
import { useNavigate } from "react-router-dom";

function EnvelopePage() {
  const navigate = useNavigate();

  const handleEnvelopeClick = () => {
    navigate("/photo"); // 사진 선택 페이지로 이동
  };

  return (

    
    <div
      style={{
        backgroundColor: "rgb(255 230 240 / 30%)", // 옅은 핑크색 배경
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column", // 세로 정렬
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {/* 초대 메시지 */}
      <h1
        className={`mb-4 fade-in`}
        style={{
          color: "black", // 글자색
          padding: "1rem", // 텍스트 주변 간격
          marginBottom: "2rem", // 텍스트와 이미지 사이 간격
        }}
      >
        ♡유라의 생일파티에 초대합니다♡
      </h1>

      {/* 봉투 이미지 */}
      <img
        src={`${process.env.PUBLIC_URL}/photos/gift-bow.png`}
        alt="envelope"
        style={{
          cursor: "pointer",
          width: "200px", // 봉투 이미지 너비
          height: "auto", // 비율 유지
        }}
        onClick={handleEnvelopeClick}
      />
    </div>
  );
}

export default EnvelopePage;